<template>
  <div class="Loader" v-if="load">
    <div class="Bar"></div>
    <div class="Bar"></div>
    <div class="Bar"></div>
    <div class="Bar"></div>
    <div class="Bar"></div>
    <div class="Bar"></div>
  </div>
  <div id="app" v-else>
    <router-view name="header"></router-view>
    <main>
      <fade-transition origin="center" mode="out-in" :duration="250">
          <router-view/>
      </fade-transition>
    </main>
    <router-view name="footer"></router-view>
  </div>
</template>

<script>
import { FadeTransition } from "vue2-transitions";

export default {
  name: 'app',
  components: {
    FadeTransition
  },
  data(){
    return {
      load: true
    }
  },
  mounted(){
    const _this = this;
    setTimeout(function(){
      _this.load = false;
    }, 3000);
  }
}
</script>



